import { LOGIN_REQUEST, LOGOUT_REQUEST } from '../types';

const initialState = {
  isLogged: false,
  user: {},
};

export default function (state = initialState, action) {
  switch (action.type) {
    case LOGIN_REQUEST:
      return {
        ...state,
        user: action.user,
      };
    case LOGOUT_REQUEST:
      return {
        ...state,
        user: {},
      };
    default:
      return state;
  }
}
